sed {\n  font-family: 'Roboto Condensed', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n}\n\n@mixin roboto-light {\n  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 300;\n  -webkit-font-smoothing: antialiased;\n}\n\n\n@mixin robotomono-light {\n  font-family: 'RobotoMono', -apple-system, monospace;\n  font-weight: 300;\n  -webkit-font-smoothing: none;\n}\n\n@mixin silkscreen {\n  font-family: Silkscreen, -apple-system, monospace;\n  font-weight: 300;\n  -webkit-font-smoothing: none;\n}\n\n// Font imports\n@font-face {\n  font-family: 'alagard';\n  src: url('../fonts/alagard.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: 'monaco';\n  src: url('../fonts/monaco.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n\n}\n\n@font-face {\n  font-family: 'RobotoMono';\n  src: url('../fonts/RobotoMono-Light.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n\n}\n/*\n  Source Sans Pro: Best for readability in dense information sections, with a clean, modern look that works well at smaller sizes.\n  Barlow: Has a slightly technical feel that matches strategy games, with excellent readability and character distinction.\n  Open Sans: Superior readability across all screen sizes and devices, with a neutral but professional appearance.\n  Rajdhani & Oxanium: Added for headings - they have a technical/sci-fi feel that complements strategy games while remaining readable.\n */\n\n\n// Import statements for recommended body text fonts\n@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@400;500;600&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@500;600;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Oxanium:wght@400;500;600&display=swap');\n\n// Body text font mixins\n@mixin source-sans {\n  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin source-sans-semibold {\n  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 600;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin barlow {\n  font-family: 'Barlow', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin barlow-medium {\n  font-family: 'Barlow', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 500;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin open-sans {\n  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.02em;\n}\n\n// Heading font mixins\n@mixin rajdhani-bold {\n  font-family: 'Rajdhani', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 700;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.03em;\n}\n\n@mixin oxanium {\n  font-family: 'Oxanium', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 500;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n// TODO: https://stackoverflow.com/questions/12562631/web-fonts-and-providing-fallback-fonts\n","@use \"sass:color\";\n@use 'sass:math' as math;\n@use 'fonts' as *;\n@use \"sass:map\" as map;\n@use \"sass:list\" as list;\n\n\n\n// Variables\n$header-font: 'Silkscreen', 'Press Start 2P', monospace;\n$description-font:  'Roboto Condensed', -apple-system, BlinkMacSystemFont, sans-serif;\n$accent-color: #9eff4a; // Adjusted to a softer yellow-green that matches the monster\n\n$text-light: #FFFFFF;\n$card-bg: rgba(0, 0, 0, 0.45);\n$border-radius: 8px;\n$page-bg: #2b222a;\n\n\n\n// Base styles\nhtml {\n  font-size: 16px;\n  min-width: 100vw;\n  margin: 0;\n  padding: 0;\n  background-color: $page-bg;\n\n  text-rendering: optimizeLegibility;\n  -webkit-font-smoothing: antialiased;\n  overflow-x: hidden; /* Prevent horizontal scrolling */\n}\n\nbody {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n\n  margin: 0;\n  padding: 0;\n  font-size: clamp(2rem, 5vw, 4rem);\n  line-height: 1.0;\n\n  @include source-sans-semibold;\n\n  font-weight: normal;\n  color: $text-light;\n  margin: 0px auto;\n  width: 100%;\n  max-width: 100vw;\n\n  letter-spacing: 0rem;\n  overflow-x: hidden; /* Hide horizontal overflow */\n  box-sizing: border-box; /* Include padding in width calculation */\n}\n\n//\nhtml, body {\n  // Prevent viewport height changes from affecting layout\n  //height: 100%;\n  overflow-x: hidden;\n\n}\n\n// Typography\nh1, h2, h3, h4, h5, h6 {\n  line-height: 1;\n  text-rendering: geometricPrecision;\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  margin-top: 0;\n  margin-bottom: 2rem;\n  font-weight: normal;\n  color: $text-light;\n  letter-spacing: 0rem;\n\n}\n\nh1, h2 {\n  @include oxanium;\n  font-size: 64px;\n  line-height: 1.6;\n  color: #f0f0f0;\n\n  // Create appropriate spacing consistent with your design\n  margin: 2em 0;\n\n  // Slightly increase letter-spacing for better readability on dark backgrounds\n  letter-spacing: 0.2px;\n\n\n  @media (max-width: 768px) {\n    font-size: 32px;\n  }\n}\n\nh3, h4, h5, h6 {\n  @include source-sans-semibold;\n  text-align: left;\n  color: #f0f0f0;\n  max-width: 70ch; // Maximum readable line length\n\n  // Fluid typography\n  font-size: clamp(1.125rem, 2vw + 0.5rem, 2rem); // Smooth scaling\n  line-height: clamp(1.3, 1.3px + (0.3 * (1vw / 10)), 1.6); // Fluid line height\n  letter-spacing: 0.01em;\n\n  // Fluid margins - more whitespace on larger screens\n  margin: clamp(1rem, 5vw, 3rem) 0; // 16px to 48px margins\n\n  &:is(h3) {\n    font-size: clamp(1.5rem, 3.0vw + 0.5rem, 2.5rem);\n  }\n\n  // Specific h4/h5/h6 adjustments - maintain hierarchy with fluid sizes\n  &:is(h4) {\n    font-size: clamp(1.25rem, 2.5vw + 0.5rem, 2.25rem);\n  }\n\n  &:is(h5) {\n    font-size: clamp(1.125rem, 2vw + 0.5rem, 2rem);\n  }\n\n  &:is(h6) {\n    font-size: clamp(1rem, 1.5vw + 0.5rem, 1.75rem);\n  }\n\n  // Base breakpoints styles\n  @media (max-width: 768px) {\n    // Content remains the same, just scaled appropriately\n    // Minimum whitespace on small screens\n    margin: 1rem 0;\n  }\n\n  // Tablet/Medium devices\n  @media (min-width: 769px) and (max-width: 1500px) {\n    // Medium whitespace\n    margin: 1.5rem 0;\n  }\n\n  // Desktop/Large devices\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    // More generous whitespace\n    margin: 2rem 0;\n  }\n\n  // Extra large devices\n  @media (min-width: 2001px) {\n    // Maximum whitespace\n    margin: 3rem 0;\n  }\n}\n\n//\n//h1 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\n//h2 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\n//h3 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\n//h4 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\n//h5 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n//h6 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n\n\n\n\nheader {\n  margin-bottom: 25px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  box-sizing: border-box;\n  overflow: hidden;\n}\nfooter {\n  background: $card-bg;\n  margin-bottom: 25px;\n  width: 100%;\n  max-width: 100vw;\n  box-sizing: border-box;\n  overflow: hidden;\n}\n\n// Links\na {\n\n  font-weight: normal;\n  text-decoration: none;\n  @include silkscreen;\n  color: $accent-color;\n\n  &:focus {\n    outline: thin dotted;\n    color: color.adjust($accent-color, $lightness: 10%);\n  }\n}\n\n// Marketing section styling\n.marketing-content {\n  //background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n\n  margin-left: auto;\n  margin-right: auto;\n\n  // Ensure minimum width for readability\n  min-width: min(300px, 90vw);\n\n  // Tablet/Medium devices\n  @media (min-width: 764px) and (max-width: 1500px) {\n    max-width: 70vw;\n  }\n\n  // Desktop/Large devices\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    max-width: 50vw;\n  }\n\n  // Extra Large devices\n  @media (min-width: 2001px) {\n    max-width: 35vw;\n    // Add a max-width in pixels to prevent excessive width on ultra-wide screens\n    max-width: clamp(35vw, 35vw, 1200px);\n  }\n}\n\n// Features Component - BEM Architecture\n// Fortifend Website\n\n.features {\n  width: 100%;\n  margin: 40px auto;\n\n  // List container\n  &__list {\n    display: grid;\n    gap: 16px;\n    margin: 0;\n    padding: 0;\n    list-style: none;\n\n    // Mobile - single column\n    grid-template-columns: 1fr;\n\n    // Tablet/Medium\n    @media (min-width: 764px) and (max-width: 1500px) {\n      grid-template-columns: repeat(2, 1fr);\n    }\n\n    // Desktop/Large\n    @media (min-width: 1501px) {\n      grid-template-columns: repeat(3, 1fr);\n    }\n  }\n\n  // Individual feature cards\n  &__item {\n    background: $card-bg;\n    border-radius: $border-radius;\n    padding: 20px;\n    transition: transform 0.3s ease, box-shadow 0.3s ease;\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n    text-align: left;\n    box-shadow: 0 4px 12px color.adjust($card-bg, $alpha: -0.5);\n    height: 100%; // Ensure equal heights\n\n    &:hover {\n      transform: translateY(-5px);\n      box-shadow: 0 6px 16px color.adjust($card-bg, $alpha: -0.4);\n\n      .features__icon {\n        transform: scale(1.1);\n      }\n\n      .features__title {\n        color: $accent-color;\n      }\n    }\n  }\n\n  // Feature icon\n  &__icon {\n    font-size: 2.5rem;\n    margin-bottom: 15px;\n    transition: transform 0.2s ease;\n    filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.5));\n\n    @media (max-width: 768px) {\n      font-size: 2rem;\n    }\n\n    @media (min-width: 1501px) {\n      font-size: 3rem;\n    }\n  }\n\n  // Feature title\n  &__title {\n    @include source-sans-semibold;\n    font-size: 1.5rem;\n    color: color.adjust($text-light, $alpha: -0.15);\n    transition: color 0.3s ease;\n    border-bottom: 2px solid rgba(158, 255, 74, 0.3);\n    padding-bottom: 8px;\n    margin-bottom: 12px;\n\n    @media (max-width: 768px) {\n      font-size: 1.25rem;\n    }\n\n    @media (min-width: 1501px) {\n      font-size: 1.75rem;\n    }\n  }\n\n  // Feature description\n  &__description {\n    @include source-sans;\n    font-size: 1rem;\n    line-height: 1.4;\n    color: color.adjust($text-light, $alpha: -0.3);\n    margin: 0;\n\n    @media (min-width: 1501px) {\n      font-size: 1.125rem;\n      line-height: 1.5;\n    }\n  }\n}\n\n.features__item:nth-child(1) { background-color: rgba(70, 60, 75, 0.5); }\n.features__item:nth-child(2) { background-color: rgba(60, 65, 80, 0.5); }\n.features__item:nth-child(3) { background-color: rgba(65, 55, 85, 0.5); }\n\n.tagline {\n  line-height: 0.0125;\n  //letter-spacing: 0.0rem;\n  //margin-bottom: 20px;\n  color: $accent-color;\n}\n\n.cta-button {\n  margin-top: 20px;\n  padding: 10px 20px;\n  background-color: $accent-color;\n  color: #000;\n  border-radius: 4px;\n  display: inline-block;\n  transition: background-color 0.3s ease;\n\n  &:hover {\n    background-color: color.adjust($accent-color, $lightness: -20%);\n  }\n}\n\n\n/* Keep your original CSS exactly as is */\n.cropped-image {\n  margin: 0px;\n  padding: 0px;\n  width: 100%; /* Container width */\n  height: 900px; /* Fixed height */\n  //height: 100vh; /* Fixed height */\n  overflow: hidden; /* Hide parts outside container */\n  /* NO position: relative here */\n  //position: relative;\n  display: contents;\n\n\n  ///* Safari-specific orientation change fix */\n  //@supports (-webkit-touch-callout: none) {\n  //  width: 100vw; /* Use viewport width instead of percentage */\n  //  margin-left: calc(-50vw + 50%); /* Center relative to the body */\n  //  box-sizing: border-box;\n  //}\n}\n\n.cropped-image img {\n  image-rendering: pixelated;\n  image-rendering: -moz-crisp-edges;\n  image-rendering: crisp-edges;\n  -ms-interpolation-mode: nearest-neighbor; /* IE */\n  /* Center the image horizontally */\n  position: absolute;\n  left: 50%;\n  transform: translateX(-50%);\n  /* Optional: adjust vertical position */\n  top: 0;\n  max-width: none;     /* IMPORTANT: Prevents the image from resizing to fit container */\n\n  //width: auto;\n  //\n  /* Set minimum width to ensure coverage on large screens */\n  min-width: 100vw; /* Always fill viewport width */\n  height: auto; /* Let height adjust based on aspect ratio */\n  min-height: 100vh; /* Make sure it fills the container height */  /* broken on android */\n\n\n  /* Position from the top for better framing */\n  top: 0;\n  z-index: -1; /* Place behind all other content */\n\n  /* Add object-fit as a fallback for browsers that support it */\n  object-fit: cover;\n  object-position: center top;\n\n  /* Mobile-specific adjustments */\n  @media (max-width: 768px) {\n    width: 100%;\n    height: 100%;\n    max-width: 100%; /* Prevent overflow */\n  }\n  //\n  ///* Android Chrome specific rules */\n  //@media screen and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {\n  //  transform: translateZ(0); /* Enable hardware acceleration */\n  //  backface-visibility: hidden;\n  //}\n\n  /* Safari-specific fix */\n  @supports (-webkit-touch-callout: none) {\n    object-fit: cover;\n    object-position: top center;\n  }\n\n  /* Add Safari-specific orientation change fix */\n  @supports (-webkit-touch-callout: none) {\n    /* Ensure image covers the full width during rotation */\n    min-width: 100vw;\n    object-fit: cover;\n    object-position: top center;\n  }\n}\n\n//\n///* Add media query for extra large displays */\n//@media (min-width: 2400px) {\n//  .cropped-image img {\n//    /* Scale up for extremely large monitors */\n//    min-width: 100%; /* Use container width */\n//    width: 100vw; /* Ensure viewport coverage */\n//    max-width: none; /* Override max-width limitations */\n//  }\n//}\n//\n///* Add only these Safari-specific fixes */\n//@supports (-webkit-touch-callout: none) {\n//  /* Only fix the container horizontal overflow in Safari */\n//  header, #header {\n//    //max-width: 100vw;\n//    //overflow-x: hidden;\n//  }\n//}\n//\n//// Define aspect ratio breakpoints\n//$aspect-ratios: (\n//        \"ultrawide\": (21, 9),  // 21:9 ultrawide monitors\n//        \"wide\": (16, 9),       // 16:9 standard widescreen\n//        \"standard\": (4, 3),    // 4:3 standard monitor\n//        \"square\": (1, 1),      // Square viewport (rare)\n//        \"portrait\": (9, 16),   // Portrait smartphone/tablet\n//        \"tall\": (3, 4)         // Tall viewport\n//);\n//\n//// Define height scales for different aspect ratios\n//$height-scales: (\n//        \"ultrawide\": 40vh,\n//        \"wide\": 35vh,\n//        \"standard\": 30vh,\n//        \"square\": 28vh,\n//        \"portrait\": 25vh,\n//        \"tall\": 22vh\n//);\n//\n//// Define object-position adjustments for different ratios\n//$vertical-positions: (\n//        \"ultrawide\": 25%,\n//        \"wide\": 30%,\n//        \"standard\": 35%,\n//        \"square\": 40%,\n//        \"portrait\": 45%,\n//        \"tall\": 50%\n//);\n//\n//// Banner position adjustments\n//$banner-positions: (\n//        \"ultrawide\": 90%,\n//        \"wide\": 85%,\n//        \"standard\": 80%,\n//        \"square\": 75%,\n//        \"portrait\": 70%,\n//        \"tall\": 65%\n//);\n//\n//// Generate aspect ratio media queries\n//@each $name, $ratio in $aspect-ratios {\n//  $width: list.nth($ratio, 1);\n//  $height: list.nth($ratio, 2);\n//\n//  // Generate min-aspect-ratio queries\n//  @media (min-aspect-ratio: #{$width}/#{$height}) {\n//    .cropped-image {\n//      height: map.get($height-scales, $name);\n//\n//      img {\n//        object-position: center map.get($vertical-positions, $name);\n//      }\n//    }\n//\n//    .banner-container {\n//      top: map.get($banner-positions, $name);\n//    }\n//  }\n//}\n//\n//// Combine with device width for more specific targeting\n//$device-breakpoints: (\n//        \"desktop\": 1200px,\n//        \"tablet\": 768px,\n//        \"mobile\": 480px\n//);\n//\n//// Nested loop for device width + aspect ratio\n//@each $device, $width in $device-breakpoints {\n//  @each $ratio-name, $ratio in $aspect-ratios {\n//    $ratio-width: list.nth($ratio, 1);\n//    $ratio-height: list.nth($ratio, 2);\n//\n//    // More specific media queries\n//    @if $device == \"mobile\" {\n//      @media (max-width: $width) and (max-aspect-ratio: #{$ratio-width}/#{$ratio-height}) {\n//        .cropped-image {\n//          // Adjust for mobile + specific aspect ratio\n//          height: map.get($height-scales, $ratio-name) * 0.8;\n//        }\n//      }\n//    } @else {\n//      @media (min-width: $width) and (min-aspect-ratio: #{$ratio-width}/#{$ratio-height}) {\n//        .cropped-image {\n//          // Adjustments for larger devices with specific aspect ratios\n//          height: map.get($height-scales, $ratio-name) * 1.1;\n//        }\n//      }\n//    }\n//  }\n//}\n//\n//// Additional loop for handling pixel scaling at different viewport sizes\n//$scale-factors: (1, 2, 3, 4);\n//$pixel-breakpoints: (\n//        \"xs\": 480px,\n//        \"sm\": 768px,\n//        \"md\": 1024px,\n//        \"lg\": 1440px\n//);\n//\n//@each $breakpoint-name, $breakpoint-value in $pixel-breakpoints {\n//  @media (min-width: $breakpoint-value) {\n//    @each $scale in $scale-factors {\n//      // Only use appropriate scales for 