2x scale\n  @media (min-width: 764px) and (max-width: 1500px) {\n    width: 600px; // Exactly 2x scaling\n  }\n\n  // Desktop/Large - 3x scale\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    width: 900px; // Exactly 3x scaling\n  }\n\n  // Extra Large - 4x scale\n  @media (min-width: 2001px) {\n    width: 1200px; // Exactly 4x scaling\n  }\n\n  /* Add Safari-specific rendering */\n  @supports (-webkit-touch-callout: none) {\n    transform: translateZ(0); /* Helps with Safari rendering */\n  }\n}\n\n.image-placeholder {\n  margin-bottom: calc(100vh * 0.10);\n}\n\n// Content sections\n.content {\n  padding: 25px 25px 25px 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.video-background {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 50vw;\n}\n\n// Gallery styles\n.gallery-container {\n  width: 100%;\n  //max-width: 2400px; // Increased for large monitors\n  margin: 0 auto;\n}\n\n.artwork-grid {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 20px;\n  justify-content: center;\n\n  // Create a proper grid layout\n  @media (min-width: 2001px) { // 4 items per row for very large screens\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(25% - 15px);\n      max-width: calc(25% - 15px);\n    }\n  }\n\n  @media (min-width: 1501px) and (max-width: 2000px) { // 3 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(33.333% - 14px);\n      max-width: calc(33.333% - 14px);\n    }\n  }\n\n  @media (min-width: 764px) and (max-width: 1500px) { // 2 items per row\n    .artwork-item {\n      //background: color.adjust($card-bg, $alpha: 0.9);\n      flex: 0 0 calc(50% - 10px);\n      max-width: calc(50% - 10px);\n    }\n  }\n}\n\n.artwork-item {\n  border-radius: $border-radius;\n  box-shadow: 0 4px 12px color.adjust($card-bg, $alpha: -0.5