\n    transition: transform 0.3s ease, box-shadow 0.3s ease;\n    overflow: hidden;\n    margin-bottom: 20px;\n    position: relative;\n    display: inline-block;\n    cursor: pointer;\n\n    // Default (mobile) styling\n    @media (max-width: 768px) {\n      flex: 0 0 100%;\n      max-width: none;\n    }\n\n    // Responsive grid layouts\n    @media (min-width: 764px) and (max-width: 1500px) {\n      flex: 0 0 calc(50% - 10px);\n      max-width: calc(50% - 10px);\n    }\n\n    @media (min-width: 1501px) and (max-width: 2000px) {\n      flex: 0 0 calc(33.333% - 14px);\n      max-width: calc(33.333% - 14px);\n    }\n\n    @media (min-width: 2001px) {\n      flex: 0 0 calc(25% - 15px);\n      max-width: calc(25% - 15px);\n    }\n\n    &:hover {\n      transform: translateY(-5px);\n      box-shadow: 0 6px 16px color.adjust($card-bg, $alpha: -0.4);\n      background: color.adjust($card-bg, $alpha: 0);\n    }\n  }\n\n  // Image\n  &__image {\n    object-fit: contain;\n    width: 100%;\n    @include pixel-perfect;\n    border-radius: $border-radius $border-radius 0 0;\n    display: block;\n    height: auto;\n  }\n\n  // Info container\n  &__info {\n    position: relative;\n    background-color: #3c3f41;\n    font-size: 2.4rem;\n    color: #fff;\n    padding: 10px 20px;\n    text-align: center;\n  }\n\n  // Text\n  &__text {\n    margin: 0 0 15px 0;\n    color: color.adjust($text-light, $alpha: -0.15);\n  }\n\n  // Fullscreen view\n  &__fullscreen {\n    display: none;\n    position: fixed;\n    justify-content: normal;\n    align-items: normal;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.9);\n    z-index: 9999;\n    opacity: 0;\n    transition: opacity 0.2s ease-out;\n\n    &--active {\n      opacity: 1;\n    }\n\n    // Fullscreen image\n    &-image {\n      max-width: none; // Override max-width to allow for custom scaling\n      max-height: none; // Override max-height to allow for custom scaling\n      @include pixel-perfect;\n      position: absolute; // Positioned by JS\n      transform-origin: 0 0; // Important for proper scaling\n\n      // Ensure Safari compatibility\n      @supports (-webkit-touch-callout: none) {\n        -webkit-transform-origin: 0 0;\n        -webkit-backface-visibility: hidden;\n      }\n    }\n\n    // Close button\n    &-close {\n      position: absolute;\n      top: 20px;\n      right: 20px;\n      width: 40px;\n      height: 40px;\n      color: white;\n      border: none;\n      border-radius: 50%;\n      font-size: 24px;\n      cursor: pointer;\n      z-index: 1;\n\n      background-color: rgba(43, 34, 42, 0.8); // Using $page-bg with opacity\n      color: $accent-color; // Using the accent-color (#9eff4a)\n      border: 1px solid rgba(158, 255, 74, 0.3); // Subtle accent-color border\n      border-radius: 50%;\n      //transition: background-color 0.2s ease, transform 0.2s ease, border-color 0.2s ease;\n\n\n      @include flex-center;\n\n\n      // Mobile styling (default)\n      top: 15px;\n      right: 15px;\n      width: 36px;\n      height: 36px;\n      font-size: 18px;\n\n      // Tablet/Medium devices\n      @media (min-width: 764px) and (max-width: 1500px) {\n        top: 20px;\n        right: 20px;\n        width: 40px;\n        height: 40px;\n        font-size: 22px;\n      }\n\n      // Desktop/Large devices\n      @media (min-width: 1501px) and (max-width: 2000px) {\n        top: 25px;\n        right: 25px;\n        width: 46px;\n        height: 46px;\n        font-size: 24px;\n      }\n\n      // Extra Large devices\n      @media (min-width: 2001px) {\n        top: 30px;\n        right: 30px;\n        width: 52px;\n        height: 52px;\n        font-size: 28px;\n      }\n\n      &:hover {\n        background-color: rgba(0, 0, 0, 0.8);\n        transform: scale(1.05);\n      }\n\n      // Ensure tap target is large enough on mobile\n      @media (max-width: 480px) {\n        // Keep minimum size for better touch accessibility\n        width: 40px;\n        height: 40px;\n        // Move slightly further from edge for easier access\n        top: 12px;\n        right: 12px;\n      }\n    }\n\n    // Instructions overlay\n    &-instructions {\n      position: absolute;\n      bottom: 20px;\n      left: 50%;\n      transform: translateX(-50%);\n      background-color: rgba(0, 0, 0, 0.6);\n      color: white;\n      padding: 10px 20px;\n      border-radius: 20px;\n      font-size: 14px;\n      opacity: 0.8;\n      z-index: 1;\n      pointer-events: none; // Allow clicking through\n      transition: opacity 0.5s ease;\n\n      &.fade {\n        opacity: 0;\n      }\n    }\n\n    // Loading indicator\n    &-loading {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      transform: translate(-50%, -50%);\n      @include flex-center;\n      z-index: 1;\n\n      &-spinner {\n        width: 40px;\n        height: 40px;\n        position: relative;\n\n        &-circle {\n          box-sizing: border-box;\n          display: block;\n          position: absolute;\n          width: 32px;\n          height: 32px;\n          margin: 4px;\n          border: 4px solid $accent-color;\n          border-radius: 50%;\n          animation: gallery-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\n          border-color: $accent-color transparent transparent transparent;\n\n          &:nth-child(1) {\n            animation-delay: -0.45s;\n          }\n\n          &:nth-child(2) {\n            animation-delay: -0.3s;\n          }\n\n          &:nth-child(3) {\n            animation-delay: -0.15s;\n          }\n        }\n      }\n    }\n  }\n}\n\n@keyframes gallery-spinner {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.icon-image {\n  height: 100px;\n  width: 100%;\n  object-fit: fill;\n  border-radius: $border-radius $border-radius 0 0;\n  filter: invert(28%) sepia(79%) saturate(2476%) hue-rotate(56deg) brightness(118%) contrast(119%);\n}\n\n.